import './text.less';
import 'time';
import { emojiData } from 'emoji';
import * as tables from 'new-table';
import * as ntables from 'tables';
import * as utils from 'utils';
import * as resourceUtils from './resourceUtils';
import * as textSideBar from './text-sidebar.pug';
import * as emoji from 'emoji';

let state: any, materialId: any, sideBar: any, textTable: any, $sideBar: any, $p: JQuery, $ctx: JQuery, $saveCtx: JQuery;

declare const kbRestUrl;
declare const appid;
declare const group;
export function initText() {
	utils.tabShown($('#text-link'), (e) => {
		init();
	});
}
function init() {
	$('#text-summary').parent().remove();
	// 
	initSideBar();
	initQqFace();
	initTable();
}
/**
 * 
 */
function initTable() {
	textTable = new tables.Table({
		el: $('#text-table'),
		options: {
			paging: true,
			serverSide: true,
			pageLength: 15,
			ajax: {
				url: `${kbRestUrl}/resource/search/${appid}`,
				method: 'POST',
				dataSrc: data => data.data,
				data: (d: any) => {
					const data = {
						group,
						appid:appid,
						title:$.trim($('#text-search-title').val()),
						page: Math.floor(d.start / d.length) + 1,
						size: d.length,
						type: 'text',
						textType:1
					};
					return utils.cleanObject(data);
				}
			},
			initComplete: initComplete,
			columns: [
				{ data: 'content', width: '70%', title: '', createdCell: tables.createAddTitle },
				{ data: 'createTime', title: '', width: (ntables as any).VARIABLES.width.commonTime, render: utils.renderCommonTime },
				{
					data: 'materialId', title: '', render: (id) => {
						return resourceUtils.renderBtn(id, 'text');
					}
				}
			]
		}
	});
}

/**
 * 
 */
function initQqFace() {
	$('#face').qqFace({
		id: 'facebox', // ID
		assign: 'text-ctx', // id
		path: `${ctx}/images/emoji/`	// 
	});
}
/**
 * 
 */
function initSideBar() {
	sideBar = new utils.SideBar({
		id: 'textSideBar',
		title: '',
		content: textSideBar(),
		width: 0.6
	});
	$sideBar = $('#textSideBar');
}
function initComplete() {
	// 
	resourceUtils.bindSearchEvent('text', () => {
		textTable.reload();
	});
	// tab
	utils.tabShown($('#text-link'), () => {
		textTable.reload();
	},false);
	$ctx = $('#text-ctx');
	$saveCtx = $('.text-save');
	$p = $('.text-ctx2 p');
	// 
	$ctx.on('input', function () {
		const addStr = $(this).val();
		view(addStr);
	});
	// 
	add();
	// 
	edit();
	// 
	save();
	// 
	resourceUtils.bindResourceDeleteEvent('text', () => {
		textTable.reload();
	});
	resourceUtils.triggerAddEvent();
}
// 
function view(str) {
	// const emojiData = getEmoji();
	str = str.replace(/\</g, '&lt;');
	str = str.replace(/\>/g, '&gt;');
	str = str.replace(/\n/g, '<br/>');
	// str
	str = str.replace(/\[.+?\]/g, function (match) {
		let result;
		let title = match;
		title = title.replace('[', '');
		title = title.replace(']', '');
		emojiData.map(function (v, i) {
			if (match === v.phrase) {
				result = `<img src="${ctx}/images/emoji/${v.url}" border="0" title=${title}/>`;
			}
		});
		return result;
	});
	$p.html(str);
}
// 
function add() {
	$('#text-add-btn').on('click', () => {
		state = 'create';
		clearForm();
		// $sideBar.find('.sidebar-title').text('');
		sideBar.updateTitle('');
		sideBar.show();
	});
}
// 
function edit() {
	$('#text-table').on('click', '.view-edit', function () {
		state = 'update';
		const el = $(this);
		const data = textTable.dt.row(el.closest('tr')).data();
		materialId = data.materialId;
		// 
		const tCtx = data.content;
		$ctx.val(tCtx);
		$p.text(tCtx);
		view(tCtx);
		// $sideBar.find('.sidebar-title').text('');
		sideBar.updateTitle('');
		sideBar.show();
	});
}
// 
function save() {
	$saveCtx.on('click', () => {
		const endLoading = utils.loadingBtn($('.link-save'));
		const ctx = $.trim($ctx.val());
		if (ctx === '') {
			utils.alertMessage('');
			endLoading();
			return;
		} else if (ctx.length > 600) {
			utils.alertMessage('600');
			endLoading();
			return;
		}
		// 
		if (state === 'create') {
			resourceUtils.resourceCreateAjax({
				type:'text',
				textType:1,
				group:group,
				nonShared:{
					content: ctx
				}
			}, (msg) => {
				textTable.reload();
				sideBar.hide();
				endLoading();
				utils.alertMessage(msg.msg, true);
			});
		} else if (state === 'update') {
			// 
			resourceUtils.resourceUpdateAjax({
				materialId:materialId,
				type:'text',
				group:group,
				nonShared:{
					content: ctx
				}
			}, (msg) => {
				textTable.reload();
				sideBar.hide();
				endLoading();
				utils.alertMessage(msg.msg, true);
			});
		}
	});
}
// 
function clearForm() {
	$ctx.val('');
	$p.text('');
	$ctx.removeClass('invalid');
}





